.footerContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.footerContainer_section {
  border-top: 1px solid #dbdbdb;
  padding: 20px 60px;
}

.footerContainer_contacts {
  display: flex;
  align-items: center;
}

.footerContainer_sources {
  font-family: "GoogleSansRegular", "Lato";
}

.footerContainer_information span {
  font-family: "GoogleSansBold";
  font-size: 20px;
}

.footerContainer_information li:first-of-type {
  margin-top: 25px;
}

.footerContainer_information li {
  margin: 20px 0;
}

.footerContainer_baublesList {
  font-size: 180%;
  display: flex;
  flex-direction: rows;
  margin-left: auto;
}

.baublesItem {
  margin-right: 10px;
}
