.Logo {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.Logo img {
  height: auto;
  max-width: 60px;
}

.Logo_text {
  font-weight: 500;
  font-family: "GoogleSansRegular", sans-serif;
  font-size: 140%;
  margin-left: 15px;
  color: white;
  line-height: 1;
}

.Logo_text__gray {
  color: #676c72;
}

.Logo_text__white {
  color: white;
}
