.spotlight {
  display: flex;
  flex-direction: column;
  border-radius: 3%;
  overflow: hidden;
  width: 300px;
  height: 350px;
  background-color: white;
  font-family: "GoogleSansRegular", "Lato";
}

.spotlight img {
  height: 180px;
}

.spotlight__content {
  flex-basis: 100%;
  padding: 16px 24px;
}

.spotlight__content span {
  font-family: "Lato";
  font-weight: 500;
  font-size: 18px;
  color: #202124;
}

.spotlight__content p {
  font-family: "Roboto";
  font-size: 14px;
  color: #5f6368;
  margin-top: 20px;
}
