.navigation__sticky {
  background-color: rgba(255, 255, 255, 0.97);
  box-shadow: 0 2px 10px #afafaf;
  padding: 0;
}

.logo {
  margin-left: 5%;
  display: flex;
  justify-content: center;
}

.navigationList {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-self: center;
}

.navigationItem {
  margin-right: 5px;
}

.navigationItem:last-child {
  margin-right: 5%;
}
