.Arrow {
  height: 70px;
  width: 70px;
  background-color: white;
  border-radius: 50%;
  font-size: 200%;
  box-shadow: 0 0 5px #afafaf;
  color: #afafaf;
  transition: box-shadow 0.2s, color 0.2s;
  position: relative;
  cursor: pointer;
}

.Arrow:hover {
  box-shadow: 0 0 15px #afafaf;
  color: black;
}

.Arrow_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
